import React from 'react'
import Link from 'gatsby-link'
import Navigoat from '../layouts/navigoat.js'

export default () =>
  <div>
    <Navigoat active_tab='projs'/>
    <div className = "container" id="top-padded">
      <div className="row" id="art_food_selector">
        <div className="col-sm-3">
          <div className="panel panel-default">
            <div className="panel-heading">
              <h3 className="panel-title">MAGE</h3>
            </div>
            <div className="panel-body">
              <a href=""><img src="../imgs/projs/mage_symbol.png" alt="MAGE"/></a>
              <p>MAGE fast approximate subgraph matching.</p>
            </div>
          </div>
        </div>
        <div className="col-sm-3">
          <div className="panel panel-default">
            <div className="panel-heading">
              <h3 className="panel-title">FastPart</h3>
            </div>
            <div className="panel-body">
              <a href="https://github.com/cjbattagl/graphpart"> <img src="../imgs/projs/fastpart_symbol_small.png" alt="FastPart"/></a>
              <p>Fast streaming graph partitioning. You can now k-partition your largest graphs in minutes using the FastPart distributed build.</p>
            </div>
          </div>
        </div>
        <div className="col-sm-3">
          <div className="panel panel-default">
            <div className="panel-heading">
              <h3 className="panel-title">TimeCelery</h3>
            </div>
            <div className="panel-body">
              <img src="../imgs/projs/celery-vis.png" alt="PSERC"/>
              <p>Time celery. Yeah, through time...Okay okay this is placeholder till I am finished making my site.</p>
            </div>
          </div>
        </div>
      </div>
      <div className = "row" id="art_food_selector">
        <div className="col-sm-3">
          <div className="panel panel-default">
            <div className="panel-heading">
              <h3 className="panel-title">Bird Migration Tracking</h3>
            </div>
            <div className="panel-body">
              <a href="https://github.com/Saganaut/com4susBOIDS"><img className="img-circle" src="../imgs/projs/tree_swallow.jpg" alt="eBird Tracking"/></a>
              <p>Tracking bird migration with network time-correlations.</p>
            </div>
          </div>
        </div>
        <div className="col-sm-3">
          <div className="panel panel-default">
            <div className="panel-heading">
              <h3 className="panel-title">Learning QWOP</h3>
            </div>
            <div className="panel-body">
              <a href=""><img className="img-circle" src="../imgs/projs/qwop.png" alt="Learning QWOP"/></a>
              <p>Using genetic algorithms and reinforcement learning to play QWOP. <em>Coming Soon.</em></p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>



